import { useEffect } from "react"
import { generateToast } from "../Util/ToastGenerator"
import { useParams } from "react-router-dom";

export default function UnsubPage() {
    let { uuid } = useParams();
    useEffect(() => {generateToast(uuid, 'error')}, [])

    return (
        <div>
            <h1>Unsub Page</h1>
        </div>
    )
}